import React, { useState } from "react";
import "./App.css";
import ExpenseForm from "./Components/ExpenseForm";
import ExpenseList from "./Components/ExpenseList";
import Alert from "./Components/Alert";
import { v4 as uuid } from "uuid";

const initialExpenses = [
 { id: uuid(), charge: "rent", amount: 1600 },
 { id: uuid(), charge: "car payment", amount: 1700 },
 { id: uuid(), charge: "credit bill", amount: 1800 },
];

function App() {
 //******************state values*************************
 const [expenses, setExpenses] = useState(initialExpenses);

 const [charge, setCharge] = useState("");

 const [amount, setAmount] = useState("");

 const [alert, setAlert] = useState({ show: false });

 const [edit, setEdit] = useState(false);

 const [id, setId] = useState(0);

 //*****************functionality******************************

 const handleCharge = (e) => {
  setCharge(e.target.value);
 };

 const handleAmount = (e) => {
  setAmount(e.target.value);
 };

 const handleAlert = ({ type, text }) => {
  setAlert({ show: true, type, text });
  setTimeout(() => {
   setAlert({ show: false });
  }, 3000);
 };

 const handleSubmit = (e) => {
  e.preventDefault();

  if (charge !== "" && amount > 0) {
   if (edit) {
    let temExp = expenses.map((item) => {
     return item.id === id ? { ...item, charge, amount } : item;
    });
    setExpenses(temExp);
    setEdit(false);
   } else {
    const singleExpense = { id: uuid(), charge, amount };
    setExpenses([...expenses, singleExpense]);
    handleAlert({ type: " success ", text: "item added" });
   }
   setCharge("");
   setAmount("");
  } else {
   handleAlert({
    type: "danger",
    text: "Charge can't be empty & amount value has to be greater than zero",
   });
  }
 };

 // clear all items
 const clearItems = () => {
  const r = window.confirm(
   "Do you really want to clear all items? (Action can't be undone)"
  );

  if (r === true) {
   setExpenses([]);
  }

  handleAlert({ type: "danger", text: "all items deleted" });
 };

 // handle delete
 const handleDelete = (id) => {
  let DeleteExp = expenses.filter((item) => item.id !== id);
  setExpenses(DeleteExp);
  console.log(DeleteExp);
  handleAlert({ type: "danger", text: "item deleted" });
 };

 // handle edit
 const handleEdit = (id) => {
  let expense = expenses.find((item) => item.id === id);
  console.log(expense);
  console.log(expenses);
  console.log(id);
  //   let { charge, amount } = expense;
  //   setCharge(charge);
  //   setAmount(amount);
  //   setEdit(true);
  //   setId(id);
 };

 return (
  <>
   {alert.show && <Alert type={alert.type} text={alert.text} />}
   <Alert />
   <h1>Budget Calculator</h1>
   <main className="App">
    <ExpenseForm
     charge={charge}
     amount={amount}
     handleAmount={handleAmount}
     handleCharge={handleCharge}
     handleSubmit={handleSubmit}
     edit={edit}
    />
    <ExpenseList
     expenses={expenses}
     handleDelete={handleDelete}
     clearItems={clearItems}
     handleEdit={handleEdit}
    />
   </main>
   <h1>
    Total Spending
    <span className="total">
     {expenses.reduce((prev, current) => {
      return (prev = prev + parseInt(current.amount));
     }, 0)}
    </span>
   </h1>
  </>
 );
}

export default App;
